
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
components: {
XHeader,
Flexbox,
FlexboxItem,
ViewBox
},
data() {
return {
contents: [],
navName: {},
years: new Date().getFullYear(),
select: {infos: '党员信息', acin: false},
userId: cookie.get('userId'),
departmentname:'',
person:0,
partyBranch:''
};
},
methods: {
sub(item){

this.$router.push({
path: '/party/partyPointDetail',
name: 'partyPointDetail',
params: {
userid:item.id
}
})
/*this.$router.push({
path: '/active/detailPack3',
name: 'detailPack3',
params:{
userId:item.id,
departmentid:item.departmentid
}
});*/
},
infor() {
axios.get('ppartymember/queryByDepartmentId', {
params: {
departmentid: this.$store.getters.user.departmentid
}
}).then(res => {
this.contents = res.data;
this.departmentname = this.contents[0].departmentname;
}).catch(err => {
console.log('fail' + err);
})

},
nav() {
axios.get('pdepartment/queryById', {
params: {
departmentid: this.$store.getters.user.departmentid
}
}).then(res => {
this.navName = res.data;
this.person = res.data.people;
}).catch(err => {
console.log('fail' + err);
})
},
judge(event) {
if (event === '1') {
return event = '男'
} else {
return event = '女'
}
}
},mounted() {
this.infor();
this.nav();
this.judge();
}

};



